/* The container */
.header-notifi__container {
  display: block;
  position: relative;
  cursor: pointer; }

/* Hide the browser's default checkbox */
.header-notifi__container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0; }

/* Create a custom checkbox */
.checkmark {
  position: absolute;
  top: -10px;
  left: -10px;
  width: 18px;
  height: 18px;
  border-radius: 4px;
  border: 1px solid #DBDBDB;
  transition: 0.3s; }

/* When the checkbox is checked, add a blue background */
.header-notifi__container input:checked ~ .checkmark {
  border: 1px solid #5273E2; }

/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
  content: "";
  position: absolute;
  display: none; }

/* Show the checkmark when checked */
.header-notifi__container input:checked ~ .checkmark:after {
  display: block; }

/* Style the checkmark/indicator */
.header-notifi__container .checkmark:after {
  left: 4px;
  top: 1px;
  width: 5px;
  height: 8px;
